import React, { Fragment, useState, useRef, useMemo, useEffect } from 'react';
import Breadcrumb from '../../layout/breadcrumb'
import {Container,Row,Col,Card,CardHeader,CardBody,ListGroup,ListGroupItem,Button,Form,FormGroup,Label,Input} from 'reactstrap'
import {Cancel} from '../../constant'
import { Typeahead } from 'react-bootstrap-typeahead'
import 'react-bootstrap-typeahead/css/Typeahead.css'
import { useDropzone } from "react-dropzone"

import { useTranslation } from 'react-i18next'
import SweetAlert from 'sweetalert2'
import useAxiosPrivate from '../../hooks/useAxiosPrivate'
import { useForm } from 'react-hook-form'
import LoadingCardSpinner from '../../components/LoadingCardSpinner/LoadingCardSpinner'
import { useLocation, useNavigate } from 'react-router-dom'

const baseStyle = {
  flex: 1,
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  padding: '20px',
  borderWidth: 2,
  borderRadius: 2,
  borderColor: '#eeeeee',
  borderStyle: 'dashed',
  backgroundColor: 'rgba(111, 162, 216, 0.2)',
  color: '#bdbdbd',
  outline: 'none',
  transition: 'border .24s ease-in-out'
}

const focusedStyle = {
  borderColor: '#2196f3'
}

const acceptStyle = {
  borderColor: '#00e676'
}

const rejectStyle = {
  borderColor: '#ff1744'
}

function getSize(x) {
  const units = ['bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
   let l = 0, n = parseInt(x, 10) || 0;
   while(n >= 1024 && ++l){
       n = n/1024;
   }
   return(n.toFixed(n < 10 && l > 0 ? 1 : 0) + ' ' + units[l]);
 }

const RenderCompileSubindicatorForm = ({ 
  subIndicator = {},
  isEditEnabled = false
 }) => {
  const { t } = useTranslation('forms')
  const { axiosPrivate, usePrivateAxiosHook } = useAxiosPrivate()
  const [isLoading, setIsLoading] = useState(false)
  const formRef = useRef()
  // form values
  const { register, handleSubmit, reset, errors, getValues } = useForm({
    defaultValues: useMemo(() => {
      return subIndicator
    }, [subIndicator])
  })

  useEffect(() => {
    reset(subIndicator)
  }, [subIndicator])

  const onSubmit = data => {
    SweetAlert.fire({
      title: t('confirmCreationMsg'),
      icon: 'info',
      showCancelButton: true,
      confirmButtonText: t('confirm'),
      cancelButtonText: t('cancel'),
      reverseButtons: true
    })
    .then((result) => {
      if (result.value) {
        if (data !== '') {
          setIsLoading(true)
          try {
            let body = {
              value: data.value,
              note: data.note
            }
            const req = `/reportperiods/subindicator/${subIndicator?.id}/`
            axiosPrivate.patch(req, body )
              .then(res => {
                // console.log(res);
                if (res.status === 201 || res.status === 200) {
                  SweetAlert.fire(
                    t('saved'),
                    t('successCreationMsg'),
                    'success'
                  )
                } else {
                  SweetAlert.fire(
                    t('somethingWentWrongMsg'),
                    t('tryAgainMsg'),
                    'warning'
                  )
                }
              })
              .catch(err => {
                // console.log('error: ', err)
                if (err.request.status === 409) {
                  SweetAlert.fire(
                    t('somethingWentWrongMsg'),
                    t('sameSubindicatorDataExist'),
                    'warning'
                  )
                } else {
                  SweetAlert.fire(
                    t('somethingWentWrongMsg'),
                    t('tryAgainMsg'),
                    'warning'
                  )
                }
              })
              .finally(() => {
                setIsLoading(false)
              })
            // console.log(body)
          } catch (e) {
            // console.log(e)
          }
        } else {
          errors.showMessages();
          setIsLoading(false)
        }
      }
    })
  }

  // handle not compilable subindicators (removed by client request but for future reasons implementation was saved)
  // if (!subIndicator?.subIndicator?.compilable) {
  //   return (
  //     <Form className="form theme-form needs-validation" autoComplete='off' onSubmit={handleSubmit(onSubmit)}  innerRef={formRef}>
  //     <CardBody>
  //       <Row>
  //         <Col>
  //           {/* not editable */}
  //           <FormGroup className="row">
  //             <Label className="col-sm-3 col-form-label">{t('name')}</Label>
  //             <Col sm="9">
  //               <div className="form-row">
  //                 <Input
  //                   className="form-control no-border"
  //                   name='subIndicator.name'
  //                   type="textarea"
  //                   innerRef={register()}
  //                   disabled
  //                 />
  //               </div>
  //             </Col>
  //           </FormGroup>
  //           <FormGroup className="row">
  //             <Label className="col-sm-3 col-form-label">{t('unitOfMeasurement')}</Label>
  //             <Col sm="9">
  //               <div className="form-row">
  //                 <Input
  //                   className="form-control no-border"
  //                   name='subIndicator.unitOfMeasurement'
  //                   type="text"
  //                   innerRef={register()}
  //                   disabled
  //                 />
  //               </div>
  //             </Col>
  //           </FormGroup>
  //           <FormGroup className="row">
  //             <Label className="col-sm-3 col-form-label">{t('parent')}: {subIndicator.subIndicator?.parent?.code ? t('indicator') : t('subindicator')}</Label>
  //             <Col sm="9">
  //               <div className="form-row">
  //                 <Input
  //                   className="form-control no-border"
  //                   name={`subIndicator.parent.${subIndicator.subIndicator?.parent?.code ? 'code' : 'name'}`}
  //                   type="text"
  //                   innerRef={register()}
  //                   disabled
  //                 />
  //               </div>
  //             </Col>
  //           </FormGroup>
  //         </Col>
  //       </Row>
  //     </CardBody>
  //     <hr />
  //   </Form>
  //   )
  // }

  return (
    <Form className="form theme-form needs-validation" autoComplete='off' onSubmit={handleSubmit(onSubmit)}  innerRef={formRef}>
      <CardBody>
        <Row>
          <Col>
            {/* not editable */}
            <FormGroup className="row">
              <Label className="col-sm-3 col-form-label">{t('name')}</Label>
              <Col sm="9">
                <div className="form-row">
                  <Input
                    className="form-control no-border"
                    name='subIndicator.name'
                    type="text"
                    innerRef={register()}
                    disabled
                  />
                </div>
              </Col>
            </FormGroup>
            <FormGroup className="row">
              <Label className="col-sm-3 col-form-label">{t('description')}</Label>
              <Col sm="9">
                <div className="form-row">
                  <Input
                    className="form-control no-border"
                    name='subIndicator.description'
                    type="textarea"
                    rows='3'
                    innerRef={register()}
                    disabled
                  />
                </div>
              </Col>
            </FormGroup>
            <FormGroup className="row">
              <Label className="col-sm-3 col-form-label">{t('unitOfMeasurement')}</Label>
              <Col sm="9">
                <div className="form-row">
                  <Input
                    className="form-control no-border"
                    name='subIndicator.unitOfMeasurement'
                    type="text"
                    innerRef={register()}
                    disabled
                  />
                </div>
              </Col>
            </FormGroup>
            <FormGroup className="row">
              <Label className="col-sm-3 col-form-label">{t('parent')}: {subIndicator.subIndicator?.parent?.code ? t('indicator') : t('subindicator')}</Label>
              <Col sm="9">
                <div className="form-row">
                  <Input
                    className="form-control no-border"
                    name={`subIndicator.parent.${subIndicator.subIndicator?.parent?.code ? 'code' : 'name'}`}
                    type="text"
                    innerRef={register()}
                    disabled
                  />
                </div>
              </Col>
            </FormGroup>
            {/* not editable */}
            <FormGroup className="row">
              <Label className="col-sm-3 col-form-label">{t('value')}*</Label>
              <Col sm="9">
                <div className="form-row">
                  <Input
                    className={`form-control ${!isEditEnabled && 'no-border'}`}
                    name='value'
                    type="text"
                    placeholder={isEditEnabled ? t('value') : ''}
                    innerRef={register({ required: true })}
                    disabled={!isEditEnabled}
                  />
                  <span className="">{errors.value && t('requiredField')}</span>
                </div>
              </Col>
            </FormGroup>
            <FormGroup className="row">
              <Label className="col-sm-3 col-form-label">{t('note')}*</Label>
              <Col sm="9">
                <div className="form-row">
                  <Input
                    className={`form-control ${!isEditEnabled && 'no-border'}`}
                    name='note'
                    type="textarea"
                    rows='3'
                    placeholder={isEditEnabled ? t('note') : ''}
                    innerRef={register({ required: true })}
                    disabled={!isEditEnabled}
                  />
                  <span className='danger-text-color'>{errors.note && t('requiredField')}</span>
                </div>
              </Col>
            </FormGroup>
            <Col sm="9 offset-sm-3 mt-4">
              {isEditEnabled && <Button color="primary" className="mr-1">{t('save')}</Button>}
            </Col>
          </Col>
        </Row>
      </CardBody>
      <hr/>
    </Form>
  )
}

const CompileIndicator = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const { t } = useTranslation('forms')
  const { indicatorSlug, indicatorId, isIndicatorCompiled, isEditEnabled } = location.state
  const fileInputRef = useRef()
  const [file, setFile] = useState([])
  const [fileSizeError, setFileSizeError] = useState(false)
  const { register, handleSubmit, errors, getValues } = useForm({ defaultValues: { isCompiled: isIndicatorCompiled } })
  const { axiosPrivate, usePrivateAxiosHook } = useAxiosPrivate()
  const [isLoading, setIsLoading] = useState(false)
  const formRef = useRef()
  const [{ data: subIndicatorsData, loading, error }, refetch] = usePrivateAxiosHook(`/reportperiods/indicator/${indicatorId}/subindicators/`)
  const [{ data: indicatorData, loading: loadingIndicator, error: errorIndicator }, refetchIndicator] = usePrivateAxiosHook(`/reportperiods/indicator/${indicatorId}/`)

  // main indicator form data
  const { register: registerMainIndicator, reset: resetMainIndicator } = useForm({
    defaultValues: useMemo(() => {
      return indicatorData?.indicator
    }, [indicatorData])
  })

  useEffect(() => {
    if (!!indicatorData?.indicator){
      resetMainIndicator(indicatorData.indicator)
    }
  }, [indicatorData])

  const onDropAccepted = (file) => {
    setFileSizeError(false)
    setFile(file)
  }

  const onDropRejected = (file) => {
    setFileSizeError(true)
  }

  const {
    acceptedFiles,
    getRootProps,
    getInputProps,
    isFocused,
    isDragAccept,
    isDragReject
  } = useDropzone({
    maxSize: 10485760,
    accept: {
      'image/*': [],
      'application/vnd.ms-excel': [],
      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet': [],
      'text/plain': [],
      'application/pdf': []
    },
    multiple: true,
    maxFiles: 10,
    onDropAccepted: onDropAccepted,
    onDropRejected: onDropRejected
  })

  const removeFile = (index) => {
    setFile(prev => {
      let newArray = [...prev]
      newArray.splice(index, 1)
      return newArray
    })
  }

  const style = useMemo(() => ({
    ...baseStyle,
    ...(isFocused ? focusedStyle : {}),
    ...(isDragAccept ? acceptStyle : {}),
    ...(isDragReject ? rejectStyle : {})
  }), [
    isFocused,
    isDragAccept,
    isDragReject
  ])

  const onSubmit = data => {
    SweetAlert.fire({
      title: t('confirmCreationMsg'),
      icon: 'info',
      showCancelButton: true,
      confirmButtonText: t('confirm'),
      cancelButtonText: t('cancel'),
      reverseButtons: true
    })
    .then((result) => {
      if (result.value) {
        if (data !== '') {
          // console.log('data: ', data);
          setIsLoading(true)
          try {
            let formData = new FormData();
            if (!!file.length) {
              file.forEach(item => {
                formData.append("files", item)
              })
            }
            formData.append("isCompiled", data.isCompiled)
            // console.log('form data is: ', formData.get('isCompiled'));
            const req = `reportperiods/indicator/${indicatorId}/`
            axiosPrivate.patch(req, formData, {
              headers: {
                "Content-type": "application/json"
              },
              onUploadProgress: function(progressEvent) {
                var percentCompleted = Math.round( (progressEvent.loaded * 100) / progressEvent.total );
                // console.log('progress: ', percentCompleted)
              }
            } )
              .then(res => {
                // console.log(res, 'res')
                if (res.status === 201 || res.status === 200) {
                  SweetAlert.fire(
                    t('saved'),
                    t('successCreationMsg'),
                    'success'
                  )
                  refetchIndicator()
                  setFile([])
                } else {
                  SweetAlert.fire(
                    t('somethingWentWrongMsg'),
                    t('tryAgainMsg'),
                    'warning'
                  )
                }
              })
              .catch(err => {
                // console.log('error: ', err)
                if (err.request.status === 409) {
                  SweetAlert.fire(
                    t('somethingWentWrongMsg'),
                    t('sameSubindicatorDataExist'),
                    'warning'
                  )
                } else {
                  SweetAlert.fire(
                    t('somethingWentWrongMsg'),
                    t('tryAgainMsg'),
                    'warning'
                  )
                }
              })
              .finally(() => {
                setIsLoading(false)
              })
            // console.log(body)
          } catch (e) {
            // console.log(e)
          }
        } else {
          errors.showMessages();
          setIsLoading(false)
        }
      }
    })
  }

  const renderFile = (item, index) => {
    return <div key={index}>{item.path} - {getSize(item.size)} <i onClick={() => removeFile(index)} className={`icofont icofont-ui-close`}></i></div>
  }

  const renderFiles = () => {
    if (!!file.length) {
      return (
        <>
          {file.map((item, index) => renderFile(item, index))}
        </>
      )
    }
  }

  // const renderFilesDownloadList = (items) => {
  const renderListItem = (item, index) => {
    return <ListGroupItem key={index} className="list-group-item-action" tag="a" href={item}>
      {`${index} - ${item.split(".").pop()}`}
    </ListGroupItem>
  }

  return (
    <Fragment>
      <Breadcrumb parent="Reporting Period" title="Compile Indicators"/>
      <Container fluid={true}>
        <Row>
          <Col sm="12">
            <Card>
              <LoadingCardSpinner isLoading={isLoading || loading || loadingIndicator} />
              <CardHeader>
                <h5>{t('compileIndicator')}</h5>
              </CardHeader>
              <Form className="form theme-form needs-validation" autoComplete='off'>
                <CardBody>
                  <Row>
                    <Col>
                      <h5>{t('previewIndicator')}</h5>
                      {/* not editable */}
                      <FormGroup className="row">
                        <Label className="col-sm-3 col-form-label">{t('code')}</Label>
                        <Col sm="9">
                          <div className="form-row">
                            <Input
                              className="form-control no-border"
                              name='code'
                              type="text"
                              innerRef={registerMainIndicator()}
                              disabled
                            />
                          </div>
                        </Col>
                      </FormGroup>
                      <FormGroup className="row">
                        <Label className="col-sm-3 col-form-label">{t('description')}</Label>
                        <Col sm="9">
                          <div className="form-row">
                            <Input
                              className="form-control no-border"
                              name='description'
                              type="textarea"
                              rows='3'
                              innerRef={registerMainIndicator()}
                              disabled
                            />
                          </div>
                        </Col>
                      </FormGroup>
                      <FormGroup className="row">
                        <Label className="col-sm-3 col-form-label">{t('unitOfMeasurement')}</Label>
                        <Col sm="9">
                          <div className="form-row">
                            <Input
                              className="form-control no-border"
                              name='unitOfMeasurement'
                              type="text"
                              innerRef={registerMainIndicator()}
                              disabled
                            />
                          </div>
                        </Col>
                      </FormGroup>
                      {!!indicatorData?.indicator?.link.trim() && 
                        <FormGroup className="row">
                          <Label className="col-sm-3 col-form-label">Link</Label>
                          <Col sm="9">
                            <div className="form-row">
                              <a 
                                target="_blank"
                                href={indicatorData?.indicator?.link}
                                style={{ width: '100%', cursor: 'pointer' }}
                              >
                                <Input
                                  className="form-control no-border"
                                  name='link'
                                  type="text"
                                  innerRef={registerMainIndicator()}
                                  disabled
                                  role="button"
                                />
                              </a>
                            </div>
                          </Col>
                        </FormGroup>
                      }
                    </Col>
                  </Row>
                </CardBody>
              </Form>
              <hr style={{ borderColor: 'black' }} />
              {subIndicatorsData?.map((item, index) => <RenderCompileSubindicatorForm key={index} subIndicator={item} isEditEnabled={isEditEnabled} />)}
              <Form className="form theme-form needs-validation" autoComplete='off' onSubmit={handleSubmit(onSubmit)}  innerRef={formRef}>
                <CardBody>
                  <Row>
                    <Col>
                      <div className="form-group row mb-0">
                        <Label className="col-sm-3 col-form-label">{t('compilationStatus')}</Label>
                        <div className="col-sm-9">
                          <div className="form-group m-checkbox-inline mb-0 ml-3">
                            <div className="checkbox checkbox-success">
                              <Input
                                id="inline-form-1"
                                type="checkbox"
                                name='isCompiled'
                                innerRef={register()}
                                disabled={!isEditEnabled}
                              />
                              <Label className="mb-0" htmlFor="inline-form-1">{t('compiledCheckboxLabel')}</Label>
                            </div>
                          </div>
                        </div>
                      </div>
                      {isEditEnabled && <FormGroup className="row mt-4">
                        <Label className="col-sm-3 col-form-label">{t('files')}</Label>
                        <Col sm="9">
                          <div className="form-row">
                            <section className="container">
                              <div {...getRootProps({style})}>
                                <input {...getInputProps()} ref={fileInputRef} />
                                <p>Drag 'n' drop files here, or click to select files (not more than 10 files)</p>
                              </div>
                              {renderFiles()}
                            </section>
                            <span className="danger-text-color">{fileSizeError && t('fileSizeError')}</span>
                          </div>
                        </Col>
                      </FormGroup>}
                    </Col>
                    <Col sm="9 offset-sm-3 mt-4">
                      {isEditEnabled && <Button color="primary" className="mr-1">{t('save')}</Button>}
                    </Col>
                    {/* uploaded files */}
                    <Col sm="12" xl="12">
                      <h5>{t('uploadedFilesListTitle')}</h5>
                      <ListGroup>
                      { !!indicatorData?.files?.length ? indicatorData?.files.map((item, index) => renderListItem(item, index))
                        : <h6>{t('uploadedFilesEmpty')}</h6>
                      }
                      </ListGroup>
                    </Col>
                  </Row>
                </CardBody>
              </Form>
            </Card>
          </Col>
        </Row>
      </Container>
    </Fragment>
  )
}

export default CompileIndicator