import React, { Fragment,useState,useEffect,useCallback,useMemo} from 'react';
import Breadcrumb from '../../layout/breadcrumb'
import DataTable from 'react-data-table-component'
import { Search } from 'react-feather'
import {Link, useNavigate} from 'react-router-dom'
import { Container,Row,Col,Card,CardHeader, CardBody, InputGroup, InputGroupAddon, InputGroupText} from 'reactstrap';
import { Typeahead } from 'react-bootstrap-typeahead'
import 'react-bootstrap-typeahead/css/Typeahead.css'
import { Input } from 'reactstrap'
import SweetAlert from 'sweetalert2'
import useAxiosPrivate from '../../hooks/useAxiosPrivate'
import LoadingCardSpinner from '../LoadingCardSpinner/LoadingCardSpinner'
import {debounce} from 'lodash'
import useAuth from '../../hooks/useAuth'
import ROLES from '../../utils/roles'
import { useTranslation } from 'react-i18next'

const CompanyList = () =>  {
  const { t } = useTranslation('dataTables')
  const { auth: { role: userRole }, auth } = useAuth()
  const navigate = useNavigate()
  const [data,setData] = useState([])

  const [loading, setLoading] = useState(false)
  const [totalRows, setTotalRows] = useState(0)
  const [perPage, setPerPage] = useState(10)
  const [currentPage, setCurrentPage] = useState(1)
  const { axiosPrivate } = useAxiosPrivate()
  const [isDeleteLoading, setIsDeleteLoading] = useState(false)

  const [searchValue, setSearchValue] = useState('')
  const [sortColumn, setSortColumn] = useState('')
  const [sortDescDirection, setSortDescDirection] = useState(false)

  const tableColumns = [
    {
        name: t('companyName'),
        selector: row => row.companyName,
        sortable: true,
        center:true,
        wrap: true,
        sortField: 'companyName'
    },
    {
        name: t('VATNumber'),
        selector: row => row.VATNumber,
        sortable: true,
        center:true,
        wrap: true,
        sortField: 'VATNumber'
    },
    {
        name: t('holding'),
        selector: row => row?.holding?.companyName,
        sortable: true,
        center:true,
        wrap: true,
        sortField: 'id_Holding'
    },
    {
      name: '',
      selector: row => renderRowBtns(row),
      sortable: false,
      center:true,
      ignoreRowClick: true
    }
  ]

  useEffect(() => {
    try {
      getCompanies()
    } catch (e) {
      // console.log(e)
    }
  }, [])

  const getCompanies = async (start = 1, limit = 10, sorting, desc, name, holding) => {
    setLoading(true)
    let params = { start, limit }
    if (holding) params = { ...params, holding }
    if (name) params = { ...params, name }
    if (sorting) params = { ...params, sorting }
    if (desc) params = { ...params, desc }
    let req = `company/`
    if ([ROLES.superadmin, ROLES.admin].includes(userRole)) {
      req = 'company/'
    } else if ([ROLES.groupleader].includes(userRole)) {
      req = `holding/${auth.holdingSlug}/company/`
    }
    const response = await axiosPrivate.get(req,
      {
        params
      }
    )
    .finally(() => {
      setLoading(false)
    })
    setData(response?.data?.results ? [...response.data.results] : [])
    setTotalRows(response?.data?.results ? response.data.count : 0)
  }

  const deleteCompany = async (slug) => {
    setIsDeleteLoading(true)
    axiosPrivate.delete(`company/${slug}/`)
      .then(res => {
        setData(prev => prev.filter(company => company.slug !== slug))
        // check to understand if element is last on the page and in this case after delete switch page behind by one
        if (totalRows - ((currentPage - 1) * perPage) === 1 && totalRows !== 1) {
          handlePageChange(currentPage - 1)
        } else {
          handlePageChange(currentPage)
        }
        SweetAlert.fire(
          t('successfullyDeleted'),
          '',
          'success'
        )
      })
      .catch(err => {
        if (err.request.status === 404) {
          SweetAlert.fire(
            t('somethingWentWrongMsg'),
            t('maybeWasAlreadyDeleted'),
            'warning'
          )
        } else {
          SweetAlert.fire(
            t('somethingWentWrongMsg'),
            t('tryAgainMsg'),
            'warning'
          )
        }
      })
      .finally(() => {
        setIsDeleteLoading(false)
      })
  }

  
  const onCompanyDeletePress = async (slug) => {
    SweetAlert.fire({
      title: t('areYouSureToDelete'),
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Ok',
      cancelButtonText: 'cancel',
      reverseButtons: true
    })
      .then((result) => {
        if (result.value) {
          deleteCompany(slug)
        }
      })
  }

  const renderRowBtns = (row) => {
    return (
      <>
        <Link
          to={`/edit-company/${row.id}`}
          state={{ companyId: row.id, companySlug: row.slug }}
          className="p-1"
          color="primary"
          size="sm"
         >
          <i className="fa fa-pencil" style={{ width: 35, fontSize: 16, padding: 11, color: 'primary' }}></i>
        </Link>
        <span onClick={() => onCompanyDeletePress(row.slug)}><i className="fa fa-trash" style={{ width: 35, fontSize: 16, padding: 11, color: '#e4566e' }}></i></span>
      </>
    )
  }

  const handlePageChange = page => {
    setCurrentPage(page)
    getCompanies((page -1) * perPage + 1, perPage, sortColumn, sortDescDirection, searchValue)
  }

  const handlePerRowsChange = async (newPerPage, page) => {
    const start = (page - 1) * newPerPage + 1
    const limit = newPerPage
    await getCompanies(start, limit, sortColumn, sortDescDirection, searchValue)
    setPerPage(newPerPage)
  }

  const onSearch = (query) => {
    getCompanies(1, perPage, false, false, query)
  }

  const debouncedSearch = React.useRef(
    debounce(onSearch, 500)
  ).current  

  const handleSort = (column, sortDirection) => {
    const isDescSort = sortDirection === 'desc'
    setSortColumn(column.sortField)
    setSortDescDirection(isDescSort)
    getCompanies(1, perPage, column.sortField, isDescSort)
  }

  return (
    <Fragment>
      <Breadcrumb parent="Company" title="Company list"/>
      <Container fluid={true}>
        <Row>
          <Col sm="12">
            <Card>
              <LoadingCardSpinner isLoading={isDeleteLoading || loading} />
              <CardHeader>
                <div className='header_filters_container'>
                  <h5 className='datatable_header_title'>{t('companies')}</h5>
                  <Row className='justify-content-end'>
                    {/* <Col sm="5" className="p-t-10 p-b-10">
                      <Typeahead
                        id="select-preset-indicators"
                        labelKey="label"
                        onChange={setHoldingSelection}
                        options={companies}
                        placeholder="Holding"
                        selected={holdingSelection}
                      />
                    </Col> */}
                    <Col sm="10" className="d-flex align-items-center p-t-10 p-b-10">
                      <InputGroup>
                        <Input
                          className="form-control align-self-center"
                          type="search"
                          placeholder={t('search')}
                          value={searchValue}
                          onChange={event => {
                            setSearchValue(event.target.value)
                            debouncedSearch(event.target.value)
                          }}
                        />
                        <InputGroupAddon addonType="append">
                          <InputGroupText style={{backgroundColor: 'inherit'}} className="form-control">
                            <Search aria-hidden="true" className="" />
                          </InputGroupText>
                        </InputGroupAddon>
                      </InputGroup>
                    </Col>
                  </Row>
                </div>
              </CardHeader>
              <CardBody>
                <DataTable
                  data={data}
                  noDataComponent={<h6 className="mb-4 mt-4">{t('noResults')}</h6> }
                  paginationComponentOptions={{rowsPerPageText: t('rowsPerPage'), rangeSeparatorText: t('of')}}
                  columns={tableColumns}
                  striped={true}
                  center={true}
                  persistTableHead
                  highlightOnHover
                  pointerOnHover
                  onChangeRowsPerPage={handlePerRowsChange}
                  onChangePage={handlePageChange}
                  paginationTotalRows={totalRows}
                  paginationServer
                  pagination
                  sortServer
                  onSort={handleSort}
                  onRowClicked={(row) => navigate(`/edit-company/${row.id}`, { state: { companyId: row.id, companySlug: row.slug } })}
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>            
    </Fragment>
  );
};

export default CompanyList;